.App {
  .sidebar {
    width: 16%;
    height: 100vh;
    background-color: #191D2B;
    position: fixed;
    border-right: 1px solid #2E344E;
    z-index: 11;
    transform-origin: left;

    @media screen and (max-width: 1000px) {
      transition: all 0.4s ease-in-out;
      transform: translateX(-100%);
      width: 30%;
    }

    @media screen and (max-width: 411px) {
      width: 50%;
    }
  }

  .navBarToggle {
    @media screen and (max-width: 1000px) {
      transition: all 0.4s ease-in-out;
      transform: translateX(0);
    }
  }

  .mainContent {
    width: 84%;
    margin-left: 16%;
    background-color: #10121B;
    min-height: 100vh;
    background-image: url(../img/dots.svg);
    display: grid;
    position: relative;

    .content {
      margin: 5rem 15rem;
      @media screen and (max-width: 1100px){
        margin: 2rem 10rem;
      }
      @media screen and (max-width: 1400px){
        margin: 3rem 10rem;
      }
      @media screen and (max-width: 1400px){
        margin: 3rem 10rem;
      }
      @media screen and (max-width: 710px){
        margin: 2rem 3rem;
      }
    }

    @media screen and (max-width: 1000px) {
      margin-left: 0rem;
      width: 100%;
    }
  }
}

.navBtn {
  position: absolute;
  z-index: 10;
  right: 10%;
  top: 10%;
  width: 4rem;
  height: 4rem;
  display: none;
  display: flex;
  align-items: center;
  justify-content: space-between;
  cursor: pointer;

  .line1, .line2, .line3 {
    height: .4rem;
    width: 100%;
    background-color: #3858CC;
    pointer-events: none;
    display: none;
    border-radius: 20px;

    &:not(:last-child) {
      margin-bottom: 0.5rem;
    }
  }

  @media screen and (max-width: 1000px) {
    display: block;

    .line1, .line2, .line3 {
      display: block;
    }
  }
}